@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.warrning-style {
  width:55vw;
  height: 100px;
  border-radius: 5%;
  background-color: rgb(247, 247, 247);
  padding: 20px;
  margin: 20px auto;
  text-align: center;
  color: rgb(141, 140, 140);
  font-size: 24px;
  font-weight: bold;
}

.wrapper {
  text-align: center;
  margin: 30px auto;
}

.navbar {
  margin-top: 50px;
  margin-bottom: 50px;
  font-size: 15px;
  padding: 0px;
  list-style: none;
  background: #fff;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  display: inline-block;
  border-radius: 50px;
}

.navbar a {
  text-decoration: none;
  color: #777;
  text-transform: uppercase;
  padding: 10px 20px;
  display: inline-block;
}

.navbar a:hover {
  color: rgb(51, 46, 46);
}
.navbar a.active {
  font-size: 1.2rem;
  color:#777;
  font-weight:500;
}

.footer {
    /* position:fixed;
    top:92vh; */
    flex-shrink: 0;
    height:80px;
    width: 100%;
    color: rgb(71, 71, 71);
    background-image: linear-gradient(rgb(252, 252, 252), rgb(223, 223, 222));
}

a {
    text-decoration: none;
    color: rgb(116, 114, 114);
}

a:hover {
    text-decoration: none;
    color: rgb(8, 8, 8);
}
p {
    color:rgb(116, 114, 114); 
    padding: 30px 20px;
}
.container-expenses {
    min-height: 430px;

}
.home-container {
  min-height: 430px;
}
.jumbotron {
  height: 20vh;
  background-color: white;
}

h5 {
  font-size: 1.2em;
  margin: 5px 15px;
  color: #777;
}
h6 {
  font-size: 1em;
  padding: 10px;
  color: #777;
  
}
button {
  margin: 5px 0 20px;
}

.instructions {
  min-height: 300px;
  padding: 10px;
  margin: 130px auto 100px;
}

.card-instruction {
  max-width: 25vw;
  width: 30vw;
  height: 200px;
  margin: 10px auto;
  border-radius: 20px;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 850px){
  .card-instruction {
    min-width: 300px;
    height: 200px;
   
  }
}

.container-income{
    min-height: 430px;
}
.income-container {
    width: 100%;
    margin: 0px;
    /* border: 2px solid grey; */
}
.income-container.container {
   margin: 0 auto;
}
.enabled-input {
    /* width: 100px; */
    border:1px solid rgb(196, 196, 197);
}
.card-body {
    padding: 0px;
    width:100%;
}

span.input-group-text{
    width: 132px;

}


input.form-control{
    display: flex;
    text-align: center;
    font-size: 1rem;
}
input.form-control:read-only{
    pointer-events: none;
    display: flex;
    text-align: center;
    color: rgb(88, 87, 87);
    background-color: rgb(247, 247, 247);
}
input.year-day.form-control {
    background-color: rgb(255, 255, 255);
}

.description-card {
    margin-bottom: 20px;
}
.text-description-card {
    padding: 0 2rem;
}




.container-budget {
    min-height: 430px;
   
}
.balance-container {
    margin: 10px 20px;
}
.not-found-container {
   min-height: 405px;
   
}

